

nav{
    /* box-shadow:  0px  3px 5px 1px rgb(242, 116, 91); */
    height: 2.9em;
    padding: 0;
    z-index:2 ;
    font-family: sans-serif;
    position: fixed;
  
    width: 100vw;

    display: flex;
    background-color: rgb(44, 43, 43);
    justify-content: space-between;
    align-items: center;

    -webkit-user-select: none;  /* Safari and Chrome */
    -moz-user-select: none;     /* Firefox */
    -ms-user-select: none;      /* Internet Explorer and Edge */
    user-select: none; 
}
nav .left {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: 3em;
    
}


nav .right{
    display: flex;
    margin-right:8em;
    flex-direction: row;
    width: 350px;
    justify-content: space-evenly;
    align-items: center;

}


.right button{
    font-size: 0.9rem;
    margin:0 1em;
    padding:0.4em 0.8em;
    color: black;
    font-weight: 500;
    background-color: rgba(239, 237, 237, 0.849);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0);
    text-decoration: none;
    box-shadow:  0px  3px 2px 1px rgb(91, 101, 242);
    /* box-shadow:  -2px  -3px 2px 2px rgb(242, 129, 91); */
}
.right button:hover{
    background-color: aliceblue;
    color: rgb(149, 0, 255);
    /* border: 1px solid red; */
    box-shadow:  0px  3px 2px 2px rgb(91, 197, 242);
}
.nav-img{
    margin-left: 1em;
    width: 8em;
    height: 2.4em;

    
}
.nav-img img{
 
    width: 8em;
    height: 2.4em;
    border-radius: 5px;
    
    
}


.nav-ul {
    position: absolute;
    color: white;
    background-color: rgb(0, 0, 0);
    right: 0em;
    top: 1.8em;
    visibility: hidden;
    padding-right: 4em;
    padding-bottom: 1.4em;
    text-decoration: none;
    /* margin: 0; */
    display: flex;
    flex-direction: column;
    align-items: start;
    border-radius:0 0px 0 20px ;
    list-style-type:none;
    transition: width 0.5s ease, height 0.5s ease, background-color 0.5s ease;
    
}

@keyframes alpha {
    from {
        visibility: hidden;
        color: aqua;
    }
    to {
        visibility: visible;
        color: white;
    }
    
}

.nav-ul  button{
    padding:0.6em 3em;
    margin: 0.2em 0em;
    margin-left: -2em;
    margin-right: -1.9em;
    font-size: 12px;
    width: 10em;
}

.divider{
    height: 1.4em;
    display: block;


}



@media screen and (max-width:560px) {
    nav .left {
        margin-left: 0;
        margin-right: 0.1em;
    }
    nav .right{
        margin-right: 0em;
    }
    .right button{
        font-size: 14px;
        margin:0 0.22em;
        padding:0.4em 0.22rem;
        font-size: 14px;
    }
 
      
    .nav-img{
        margin-left: 1em;
        width: 5em;
        height: 2em;
    }
    .nav-img img{
 
        width: 5em;
        height: 2em;
        border-radius: 5px;
        
    }

}