.content-container, .content-container2, .content-container3{
    background-color: black;
    /* position: absolute; */
    font-family: sans-serif;
 
    -webkit-user-select: none;  /* Safari and Chrome */
    -moz-user-select: none;     /* Firefox */
    -ms-user-select: none;      /* Internet Explorer and Edge */
    user-select: none;  
    height: max-content;
    margin-bottom: 4.3em;
}




.content-container p , .content-container2 p , .content-container3 p {
   margin:0 1em ;
   margin-top: 1.2em;
   font-style: italic;
   /* height: 8em; */
   text-align: center;
   font-size: 2.4em;
   padding:0.8em 1em;
   font-weight: 600;
   border-bottom: 1px solid rgb(255, 166, 0);
   box-shadow:  -2px  2px 2px 2px rgba(255, 0, 0, 0.826);
   /* outline: 2px solid red; */
   color: rgb(246, 153, 66);
   /* box-shadow: 0 0 20px 1px rgb(255, 248, 248); */
   border-radius: 10px;
}
/* .content-container1 p{
    height: 15em;
} */

.content-container2 p{
    color: rgb(114, 246, 66);
    /* padding:2em 0; */
    /* height: 10em; */
    border-bottom: 1px solid rgb(0, 94, 255);
   box-shadow:  -2px  2px 2px 2px rgba(255, 0, 238, 0.782);
}

.content-container3 p{
    color: rgb(254, 252, 252);
    /* padding:2em 0; */
    /* height: 10em; */
    border-bottom: 1px solid rgb(0, 94, 255);
   box-shadow:  -2px  2px 2px 2px rgb(210, 234, 186);
}

.content-container2 li , .content-container2 h2{
    font-size: 15px;
    margin: 0;
    padding: 5px;
    /* align-items: start; */
    /* text-align: start; */
}

.content-container2 ul{
    margin-left: 5em;
}
/* .content-container2 ul { */
    /* font-size: 20px; */
    
    /* background-color: aqua; */
/* } */
.content-container2 li{
    text-align: start;
    list-style:circle;
    font-size: 0.9em;
    font-weight: 800;
    /* transform: translateX(-2em); */
}
.content-container2 h2{
    text-align: start;
    font-size: 1.5em;
    color: rgb(90, 183, 249);

}


.content-container p:active , .content-container2 p:active , .content-container3 p:active{
    background-color: rgba(8, 53, 255, 0.203);
    border-bottom: 1px solid rgb(0, 255, 30);
   box-shadow:  -2px  2px 2px 2px rgb(251, 128, 128);
}

/* .content-container p:hover , .content-container2 p:hover , .content-container3 p:hover{
    background-color: rgba(8, 53, 255, 0.203);
    border-bottom: 1px solid rgb(0, 255, 30);
   box-shadow:  -2px  2px 2px 2px rgb(251, 128, 128);

} */


.content-container3 table{
    text-align: center;
    margin:auto;
}
.content-container3 th{
    color: red;
    width: 16em;
    font-size: 1.3em;
    font-weight: 800;
    text-align: left;
    box-shadow:  -2px  2px 2px 1px rgb(128, 232, 251);
}
.content-container3 td{
    /* border: 1px solid rgb(13, 243, 13); */
    border-radius: 6px;
    color: rgb(226, 250, 248);
    box-shadow:  -2px  2px 2px 2px rgb(251, 128, 128);
    
}

@media screen and (min-width:600px) {
    .content-container p:hover , .content-container2 p:hover , .content-container3 p:hover{
        background-color: rgba(8, 53, 255, 0.203);
        border-bottom: 1px solid rgb(0, 255, 30);
       box-shadow:  -2px  2px 2px 2px rgb(251, 128, 128);
    
    }
    
}



@media screen and (max-width:700px) {
    .content-container p , .content-container2 p , .content-container3 p {
        margin-top: -30px;
        /* height: 11em; */
        font-size: 18px;
    }
    
    .content-container2 li{
        font-size: 15px;
    }

    .content-container2 ul{
        margin-left: unset;
    }
}